<template>
  <div>
    <nav-head title="意见反馈" :fn="goExit">
      <span slot="right" class="question-icon nav-right" @click="saveEdit" >发送</span>
    </nav-head>
    <div class="feed-back">
      <textarea maxlength="200" @input="handleInputCount" v-model="cont" placeholder="填写你遇到的问题或一些建议！"></textarea>
      <span class="count">{{counts}}/200</span>
    </div>
    <div class="cancel-bind" :class="{show: show}">
      <div class="mask" @click="closeCancelBind"></div>
      <div class="content">
        <div class="message">确认退出吗？<br>已填写的信息不会被保留</div>
        <div class="btn-group">
          <mt-button @click.native="goBack" class="cancelButton">退出</mt-button>
          <mt-button @click.native="closeCancelBind" class="confirmButton">继续填写</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { Button, Toast } from 'mint-ui'
import http from '../../tool/http'

export default {
  name: 'feedBack',
  data () {
    return {
      cont: '',
      show: false,
      counts: 0
    }
  },
  methods: {
    handleInputCount (e) {
      this.counts = e.target.value.length
    },
    closeCancelBind () {
      this.show = false
    },
    saveEdit () {
      if (this.counts > 0) {
        http.postAdvice({ content: this.cont }).then(data => {
          Toast({
            message: '意见提交成功!',
            duration: 1000
          })
          setTimeout(() => {
            this.$router.go(-1)
          }, 1000)
        })
      }
    },
    goExit () {
      if (this.counts > 0) {
        this.show = true
      } else {
        this.$router.go(-1)
      }
    },
    goBack () {
      this.show = false
      this.$router.go(-1)
    }
  },
  components: {
    NavHead,
    [Button.name]: Button
  }
}
</script>
<style scoped lang="scss">
  .feed-back{
    padding: 15px 15px;
    textarea {
      display: block;
      width: 100%;
      padding: 5px 10px;
      border-radius: 5px;
      height: 270px;
      font-size: 14px;
      border: none;
      resize: none;
      line-height: 22px;
      outline: none;
      background-color: $background-light-color;
      color: $font-color;
    }
    .count {
      font-size: 12px;
      float: right;
      margin-top: -20px;
      margin-right: 10px;
      color: #999;
    }
  }

  .cancel-bind {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    max-width: 640px;
    top: 0;
    display: none;
    .mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,0.5);
      z-index: 2;
    }
    .content {
      position: relative;
      z-index: 3;
      width: 70%;
      margin-left: 15%;
      background-color: white;
      border-radius: 6px;
      padding: 15px 0;
      top: calc((100vh - 162px)/2);
      text-align: center;
      .message {
        font-size: 16px;
        color: #333;
        font-weight: bold;
        line-height: 26px;
        margin: 15px 0 25px 0;
      }
      .cancelButton{
        background: #DDD ;
        width: 40% ;
        height: 40px;
        color: #999;
        margin: 0 15px 0 0 ;
        font-size: 16px;
      }
      .confirmButton{
        width: 40% ;
        height: 40px;
        font-size: 16px;
        background-color: $back-color;
        @if $IF365 { color: #131313; }
        @else { color: white; }
      }
    }
  }
  .cancel-bind.show {
    display: block;
  }
</style>
